<template>
	<div class="main">
		<van-search fixed  
		background="#21b276" maxlength="20" label="位置" placeholder="来喝杯咖啡吧" shape="round">
		<template #left-icon  >
			<van-icon name="location-o" @click="getLocat"></van-icon>
		</template>
		</van-search>
		<div class="mainbox">
			<div class="toptips">
				<p class="BusHours">营业时间</p>
				<div class="BusTime">早上 10:00 -- 晚间 22:00</div>
				<div class="greetings">
					<span class="ting">{{nowTimer}}。</span>
					<div class="place">附近门店 :<span>吉山</span></div>
				</div>
			</div>
			<!-- 轮播图 -->
			<van-swipe @change="onChange" autoplay="3000">
				<van-swipe-item v-for="(item,index) in bannerData" :key="index">
					<img class="auto-img" :src="item.bannerImg" />
				</van-swipe-item>
				<template #indicator>
					<div class="indicator-box">
						<div class="indicator-item" :class="{active:current===i}" v-for="(v,i) in bannerData" :key="i"></div>
					</div>
				</template>
			</van-swipe>
			<!-- 商品列表 -->
			<div class="comList">
				<div class="comList-tips">热销商品</div>
				<ul>
					<li v-for="(item,index) in getHotComData " :key="index" @click="goDetail(item.pid)">
						<div class="comIntroduce">
							<div class="comName">{{item.name}}</div>
							<span class="comDetails">{{item.desc}}</span>
						</div>
						<div class="comImg">
							<img class="auto-img" :src="item.largeImg" alt="">
						</div>
					</li>
				</ul>
			</div>
		</div>
		<van-popup v-model="isShow" id="map">
			<Map></Map>
		</van-popup>
	</div>
</template>

<script>
	import "../assets/less/Main.less"
	import Map from "../components/Map.vue"
	import {
		mapState,
		mapMutations
	} from "vuex";
	export default {
		name: "Main",
		data() {
			return {
				current: 0,
				bannerData: [],
				getHotComData: [],
				isShow:false,
				
			}
		},
		created() {
			//生成data,但是没有$el(没有渲染--没生成div)
			this.getBannerData();
			this.getHotCom()
			this.getShopbagCount();
		},
		components:{
			Map
		},
		computed: {
			...mapState(["shopbagCount", "isLoadShopbagCount"]),
			nowTimer: () => {
				let date = new Date()
				let hours = date.getHours()
				let greetings = ""
				if (hours >= 6 && hours <= 12) {
					greetings = "早上好"
				}
				if (hours >= 12 && hours <= 18) {
					greetings = "下午好"
				}
				if (hours >= 18 && hours <= 24) {
					greetings = "晚上好"
				}
				if (hours >= 1 && hours <= 6) {
					greetings = "深夜好"
				}
				return greetings
			}

		},
		methods: {
			
			// 打开地图
			getLocat(){
				this.isShow=!this.isShow
			},
			...mapMutations(['changeShopbagCount', "changeIsLoadShopbagCount"]),
			//切换轮播图
			onChange(index) {
				this.current = index;
			},


			//获取轮播图数据

			getBannerData() {
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'GET',
					url: '/banner',
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					if (res.data.code == 300) {
						// console.log(res);
						this.bannerData = res.data.result
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},

			//获取热销商品
			getHotCom() {
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'GET',
					url: '/typeProducts',
					params: {
						key: 'isHot',
						value: 1
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 500) {
						this.getHotComData = res.data.result
						// console.log(this.getHotComData );
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},

			//获取购物袋提示数据
			getShopbagCount() {
				if (this.isLoadShopbagCount) {
					// console.log("已经加载过购物袋数量");
					return;
				}
				//获取用户token值
				let tokenString = this.$cookies.get("tokenString");

				//如果token为空,说明用户没有登录或未注册
				if (!tokenString) {
					console.log("没有token值");
					return
				}
				this.$toast.loading({
					message: "加载中...",
					forbidClick: true,
					duration: 500,

				});
				//成功则请求
				this.axios({
					method: "GET",
					url: "/shopcartRows",
					params: {
						tokenString: tokenString
					}
				}).then((res) => {
					// console.log("res==>",res);
					// 清除登录账户密码
					this.$toast.clear();

					//如果没有登录
					if (res.data.code === 700) {
						return;
					}
					if (res.data.code === 8000) {
						//说明加入成功,改变购物袋显示数字
						this.changeShopbagCount(res.data.result);
						this.changeIsLoadShopbagCount(true);
					}
				}).catch((err) => {
					this.$toast.clear();
					console.log("err==>", err);
				})
			},
			goDetail(pid) {
				this.$router.push({
					name: "ProductDetails",
					params: {
						pid
					}
				});
			}

		}
	}
</script>
